*{
  user-select: none;
}
.cellBoard{
  outline-offset:1px;
  outline-width: 1px;
  outline-style: ridge;
  width: 40px;
  height: 40px;
  border: 1px solid rgb(114, 114, 114);
  background-color: white;
  border-radius: 4px;
  cursor: pointer;
  
}
.clearGame{
  pointer-events: none;
}
.optionSize{
  position: relative;
}
.optionSize::after{
  content: attr(custom-attribute);
  position: absolute;
  width: 100px;
  padding: 5px;
  font-size: 16px;
  left: 30%;
  top: -20px;
  border: 2px solid rgb(230, 173, 5);
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
}
.winGame{
  font-family: Lucida Console, Courier, monospace;
  font-size: 30px;
  position: absolute;
  left: 0;
  top: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}
.winGame p{
  cursor:auto;
  pointer-events:none;
  position: absolute;
  left: 35%;
  top: 30%;
  border: 3px solid rgb(128, 128, 128);
  padding: 40px;
  border-radius: 10px 10px 50px 50px;
  background-color: rgb(233, 233, 233);
}
.gg3:hover{
  background-color: azure;
}
.lineBoard{
  display: flex;
}
.game{
  display: flex;
  margin-top: 50px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
  
}
.gameOptionMenu{
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 200px;
  height: 200px;
  margin-right: 50px;
  border: 2px solid black;
  border-radius: 5px;
}
.gameOptionMenu p:last-child{
 padding: 10px;
 cursor: pointer;
 border-radius: 20px;
}
.gameOptionMenu p:last-child:hover{
  background-color: rgba(192, 194, 194,0.5);
}
.shareBoard{
  outline-offset:-6px;
  outline-width: 2px;
	outline-style: solid;
  border: 5px solid saddlebrown;
  border-radius: 4px;
  box-shadow:0px 0px 5px 7px rgb(114, 114, 114);
}
.gg1{
  background-color: rgb(42, 211, 48);
  cursor:no-drop
}
.gg2{
  background-color: rgb(255, 217, 0);
  cursor:no-drop
}
.style{
  border:4px solid seagreen
  
}
.begin{
  height: 100vh;
}
.menu{
  display: inline-block;
  height: 100%;
  width: 100%;
  margin-top: 20%;
  font-size: 30px;

}
.inner {
  padding: 10px;
  transition: color ease-in-out 0.5s ;
  cursor: pointer;
}
.menu span{
  font-size: 20px;
  font-weight: 100;
  padding: 6px;
  border: 2px solid rgba(114, 114, 114,1);
  border-radius: 5px;
  margin-right: 10px;
}
.share{
  padding-top: 10%;
  font-size: 50px;
  font-weight: bold;
}
.inner:hover{
  color: black;
}
.inputOption{
  outline: none;
  padding: 10px 5px 10px 5px;
  
  border: 2px solid rgb(114, 114, 114);
  border-radius: 5px;
  width: 40px;
  font-size: 17px;
  font-weight: 100;
  text-align: center;
  background-color: rgba(205, 205, 205, 0);
}
.errorInput{
  border: 2px solid red;
}
.menu .inner:nth-child(2){
  position: relative;
}
